	//api
	agentCommonApi: function(postData, url) {
		uni.showLoading({
			title: ''
		})
		postData.sign = this.setFormData(postData);

		return new Promise((resolve, reject) => {
			uni.request({
				url: url,
				data: postData,
				method: "POST",
				header: {
					'Accept': 'application/json',
					'content-type': 'application/x-www-form-urlencoded' // 
				},
				success(res) {
					uni.hideLoading();
					console.info("");
					console.info(res);
					if (res.statusCode != 200) {
						uni.showToast({
							title: '',
							duration: 2000,
							success() {}
						})
					} else if (res.data.resultCode == '00000') {
						resolve(res)
					} else {
						if (url.indexOf("autoLogin") != -1) {
							resolve(res)
						} else {
							uni.showToast({
								title: res.data.message,
								duration: 2000,
								success() {
									setTimeout(function() {
										if (res.data.resultCode == 'A0230') {
											uni.navigateTo({
												url: '/pages/login/index'
											})
										}
									}, 2000)
								}
							})
						}
					}
				},
				fail(res) {
					uni.hideLoading();
					uni.showToast({
						title: '',
						duration: 2000,
						success() {}
					})
				}
			});
		})


	},

	//
	setFormData: function(obj) {
		var that = this;
		var newkey = Object.keys(obj).sort();
		var signStr = "";
		for (var i in newkey) {
			var s = newkey[i];
			signStr = signStr + newkey[i] + obj[s];
		}
		var sign = md5Util.md5(signStr + "md5Key" + commonConst.md5Key);
		return sign;
	},
	//
	mathRand: function() {
		var num = '';
		var y = 6;
		for (var i = 0; i < 6; i++) {
			num += Math.floor(Math.random() * 10);
		}
		var time = (new Date()).getTime();

		return num + time;
	}

